.dropdown-menu {
  display: none;
  z-index: 1002;
  color: white;
  background: #61617C;
  border: #222348;
  border-radius: 5px;
  border-width: 3px;
  padding: 5px;
}

.dropdown-menu li {
  list-style: none;
}

.dropdown-menu a {
  border: 0;
  cursor: pointer;
}

h1 {
  padding-bottom: 1em;
  border-bottom: solid 2px rgba(255, 255, 255, 0.5);
}

.subalbums {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  margin: 1em;
  font-size: 12pt;
}

.subalbums .subalbum {
  position: relative;
  width: 150px;
  height: 150px;
  margin: 0 1px 1px 0;
  float: left;
  cursor: pointer;
}

.subalbums .subalbum a {
  border: 0;
}

.subalbums .subalbum a img {
  margin: 0;
}

.subalbums .subalbum .subalbumname {
  position: absolute;
  bottom: 0;
  width: 100%;
  padding: 5px 2px 5px 2px;
  text-align: center;
  font-weight: bold;
  color: black;
  background-color: white;
  opacity: 0.8;
}

.subalbums .subalbum:hover {
  text-decoration: none;
}

.subalbums .subalbum:hover .subalbumname {
  opacity: 0.9;
}

.subalbums .subalbum,
.jgallery-container-inner a {
  transition: opacity .4s ease-out;
}

.subalbums:hover .subalbum,
.jgallery-container-inner:hover a {
  opacity: 0.8;
}

.subalbums:hover .subalbum:hover,
.jgallery-container-inner:hover a:hover {
  opacity: 1;
}

.jgallery {
  background-color: #fff !important;
}

.jgallery .zoom-container .jgallery-container {
  background: none !important;
}

.jgallery .jgallery-thumbnails .jgallery-container {
  overflow-x: auto;
}

.jgallery .jgallery-thumbnails .jgallery-container img {
  margin: 0;
}

.jgallery .jgallery-thumbnails.full-screen .jgallery-container {
  overflow-y: auto;
}

